/* TOP LINKS SECTION START */

.nav_links_section {
  padding: 10px 2rem;
  max-width: 100%;
  background: #f1f3f5;
  align-items: center;
  justify-self: center;
  align-self: center;
  margin: 1.78rem 10rem 5px 10rem;

  display: flex;
  justify-content: space-between;
  /* z-index: 10; */
}

.nav__links {
  display: flex;
  align-items: center;
  grid-gap: 4rem;
  gap: 4rem;
  transition: -webkit-transform;
  transition: transform;
  transition: transform, -webkit-transform;
}

.nav_link {
  font-family: var(--font-base);
  color: var(--color-black);
  font-weight: 600;
  letter-spacing: 0.04em;
  text-transform: capitalize;
  line-height: 28px;
  font-size: 16px;
}

.contectus_nub {
  display: flex;
  align-items: center;
  grid-gap: 3px;
  gap: 3px;
  font-size: 16px;
  color: #0c8599;
  font-family: var(--font-base);
  font-weight: 600;
}

/* TOP LINKS SECTION END */

.banner_contectus_section {
  display: flex;
  grid-gap: 10px;
  gap: 10px;
  justify-content: center;
  align-items: baseline;
}

.contectus_btn {
  font-size: 16px;
  font-family: var(--font-base);
  font-weight: 500;
  padding: 5px 10px;
  background: #0c8599;
  color: #fff;
  border-radius: 20px;
  border: none;
  cursor: pointer;
}

/* BANNER OVERLY START */

.banner_overly_wrapper {
  /* position: absolute; */
  /* margin-top: 1rem; */
}

/* BANNER OVERLY END */

/* HOVER EFFECTS START */

.nav__links a:hover {
  color: #22b8cf;
  border-bottom: 2px solid #22b8cf;
  transition: 0.3s ease color;
  text-decoration: none;
  -webkit-transform: scale(0.1px);
          transform: scale(0.1px);
}

.nav_alt_logo:hover {
  transition: 0.4s ease;
  color: #22b8cf;
}

/* HOVER EFFECTS END */

